*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* loader */
.spinner{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 5;
    background-color: rgba(0, 0, 0, 0.208);
}

.form-btn {
    width: 250px !important;
    margin-top: 30px !important;
}
.main{
    padding: 10px;
    height: 100vh;

}

.layout{
    display: flex;
}

.sidebar{
    min-height: 100%;
    width: 20vw;
    border-radius: 5px;
    background-color: rgba(0, 0, 0, 0.886);
    box-shadow: 0 0 2px gray;
    margin-right: 10px;
    color: white;
}

.content{
    height: 100%;
    width: 100%;
}

.header{
    height: 10vh;
    margin-bottom: 5vh;
    box-shadow: 0 0 2px gray;
    background-color: white;
}

.body{
    height: 85vh;
    margin-bottom: 5vh;
    box-shadow: 0 0 2px gray;
    background-color: white;
}

.logo h3{
    margin: 20px 0;
    text-align: center;
    font-size: 1.5rem;
}

.menu {
    margin-top: 10vh;
}

.menu-item {
    margin-top: 30px;
}

.menu-item a{
    color: white;
    text-decoration: none;
    font-size: 1.2rem;
}

.menu-item i{
    font-size: 1.2rem;
    margin: 0 1.3vw;
}

.active {
    background-color: white;
    padding: 10px;
}

.active a, .active i{
    color: rgba(0, 0, 0, 0.886);
}

.header-content{
    margin: 0 15px;
    height: 8vh;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.header-content i {
    margin-right: 1vw;
    font-size: 1.5rem;
}

.header-content a {
    text-decoration: none;
    -webkit-column-rule: blue;
            column-rule: blue;
    margin: 0 10px;
    font-size: 1.3rem;
    text-transform: uppercase;
}

.form-container{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    border: 1px solid black;
}

.register-form{
    border: 1px solid gray;
    padding: 4vw;
    width: 30%;
}
